import React from "react";
import { DebounceInput } from "react-debounce-input";
import { CountrySelect } from "./countrySelect";
import "./address.css";

export const SearchAddress = ({
  selectedAddress = {},
  addressList = [],
  setSelectedCountry,
  countries,
  handleChange = () => {},
  handleSelect = () => {},
  selectedCountry = {
    value: "IN",
    label: "🇮🇳 India",
  },
  showCountrySelect = true,
}) => {
  // TODO - REMOVE COMMENTED CODE AND ADD CLASSESS FOR INLINE CSS
  return (
    <div style={{ display: "flex", justifyContent: "space-between" }}>
      {showCountrySelect && (
        <div style={{ flexBasis: "35%" }}>
          <CountrySelect
            countries={countries}
            setSelectedCountry={setSelectedCountry}
            selectedCountry={selectedCountry}
          />
        </div>
      )}

      <div
        style={showCountrySelect ? { flexBasis: "65%" } : { flexBasis: "100%" }}
      >
        <div style={{ width: "100%", borderRadius: 2 }}>
          <DebounceInput
            className="form-control"
            placeholder="search here..."
            minLength={1}
            debounceTimeout={500}
            onChange={(event) => handleChange(event.target.value)}
            value={selectedAddress.title}
          />
        </div>
        {addressList && addressList.length > 0 && (
          <div
            style={{
              maxHeight: 100,
              overflowY: "scroll",
              borderRadius: 5,
              border: "1px solid lightgray",
            }}
          >
            {addressList.map((item, index) => {
              return (
                <li
                  className="address-dropdown"
                  key={index}
                  onClick={(e) => handleSelect(e.target.value, item)}
                >
                  {item.title}
                </li>
              );
            })}
          </div>
        )}
      </div>
    </div>
  );
};
