import React, { useCallback, useState } from "react";
import { Form } from "react-bootstrap";
import axios from "axios";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import { HOST, MAPS_API_URL } from "../../../constants";
import { SearchAddress } from "../../commonComponents/user/AddressComponent";
import { COUNTRIES } from "../../../countryCodeMapping";
import "../signup.css";
import { HomeHeader } from "../../home/header";

export const SignUp = ({ history }) => {
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    password: "",
    email: "",
    contact: "",
    // address: "",
    pincode: "",
  });
  const [errors, setErrors] = useState({});
  // Search address Component props  - Requires special handling
  const [address, setAddress] = useState("");
  const [selectedAddress, setSelectedAddress] = useState("");
  const [addressList, setAddressList] = useState([]);
  const [selectedCountry, setSelectedCountry] = useState({
    value: "IN",
    label: "🇮🇳 India",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
    // Resetting errors
    if (formData[name]) {
      errors[name] = "";
      setErrors(errors);
    }
  };

  const validateForm = () => {
    let isValid = true;
    const newErrors = {};

    // Validate name
    if (!formData.firstName.trim()) {
      newErrors.firstName = "Name is required";
      isValid = false;
    }

    // Validate username
    if (!formData.lastName.trim()) {
      newErrors.lastName = "last name is required";
      isValid = false;
    }

    // Validate address
    if (!address.trim()) {
      newErrors.address = "address is required";
      isValid = false;
    }

    // Validate pincode
    if (!formData.pincode) {
      newErrors.pincode = "pincode is required";
      isValid = false;
    }

    // Validate password
    if (!formData.password.trim()) {
      newErrors.password = "Password is required";
      isValid = false;
    }

    // Validate email
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!formData.email.trim() || !emailRegex.test(formData.email)) {
      newErrors.email = "Enter a valid email address";
      isValid = false;
    }

    // Validate phone number
    const phoneRegex = /^\d{10}$/;
    if (!formData.contact.trim() || !phoneRegex.test(formData.contact)) {
      newErrors.contact = "Enter a valid 10-digit phone number";
      isValid = false;
    }

    setErrors(newErrors);
    return isValid;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    // Add validations here - Create a function
    if (!validateForm()) {
      // If form is valid, you can proceed with signup logic here
      // Show toast here
      console.log("Invalid form");
    }
    // Api call to send create user
    try {
      let response = await axios.post(`${HOST}/api/user/signup/`, {
        ...formData,
        address: selectedAddress,
      });
      console.log("response signup", response);
      if (response.status === 201) {
        // history.push("/user/home", { userData: response?.data || {} });
        history.push("/user/login");
        console.log("response", response);
      }
    } catch (error) {
      console.log("error", error);
    }
  };

  // Address helper functions

  const handleAddressChange = useCallback(
    async (value) => {
      // let countryCode = selectedCountry.value;
      let response = [];

      setAddress(value);
      if (value) {
        errors[address] = "";
        setErrors(errors);
        // GEO code api
        response = await axios
          .get(MAPS_API_URL, {
            params: {
              q: value,
            },
          })
          .then((res) => res.data);
        console.log("response", response);
        setAddressList(response?.items || []);
      } else {
        setAddressList([]);
      }
    },
    [address, selectedAddress, addressList]
  );

  const handleAddressSelect = (e, item) => {
    setSelectedAddress(item);
    setAddressList([]);
  };

  return (
    <div>
      <HomeHeader />
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          backgroundColor: "rgba(0, 0, 0, 0.3)",
          padding : 20
        }}
      >
        <Form
          className="shadow p-4 bg-white rounded"
          style={{flexBasis: "65%" }}
        >
          <h3>Sign Up</h3>
          <div className="m-2">
            <label>First name</label>
            <input
              type="text"
              name="firstName"
              onChange={handleChange}
              className="form-control"
              placeholder="First name"
            />
            {errors.firstName && (
              <span className="red-text">{errors.firstName}</span>
            )}
          </div>
          <div className="m-2">
            <label>Last name</label>
            <input
              type="text"
              onChange={handleChange}
              name="lastName"
              className="form-control"
              placeholder="Last name"
            />
            {errors.lastName && (
              <span className="red-text">{errors.lastName}</span>
            )}
          </div>
          <div className="m-2">
            <label>Email address</label>
            <input
              onChange={handleChange}
              type="email"
              name="email"
              className="form-control"
              placeholder="Enter email"
            />
            {errors.email && <span className="red-text">{errors.email}</span>}
          </div>
          <div className="m-2">
            <label>Contact</label>
            <input
              onChange={handleChange}
              type="number"
              name="contact"
              className="form-control"
              placeholder="Enter contact"
            />
            {errors.contact && (
              <span className="red-text">{errors.contact}</span>
            )}
          </div>
          <div className="m-2">
            <label>Password</label>
            <input
              onChange={handleChange}
              type="password"
              name="password"
              className="form-control"
              placeholder="Enter password"
            />
            {errors.password && (
              <span className="red-text">{errors.password}</span>
            )}
          </div>
          <div className="m-2">
            <label>address</label>
            {/* Commenting this out for special handling */}
            {/* <textarea
            name="address"
            className="form-control"
            placeholder="Enter address"
            onChange={handleChange}
          /> */}

            <SearchAddress
              showCountrySelect={false}
              address={address}
              selectedAddress={selectedAddress}
              addressList={addressList}
              setSelectedCountry={setSelectedCountry}
              countries={COUNTRIES}
              handleChange={handleAddressChange}
              handleSelect={handleAddressSelect}
              selectedCountry={selectedCountry}
            />
            {errors.address && (
              <span className="red-text">{errors.address}</span>
            )}
          </div>
          <div className="m-2">
            <label>zip code</label>
            <input
              onChange={handleChange}
              type="number"
              name="pincode"
              className="form-control"
              placeholder="Enter zipcode"
            />
            {errors.pincode && (
              <span className="red-text">{errors.pincode}</span>
            )}
          </div>
          <div className="d-grid">
            <button
              type="submit"
              className="btn btn-primary"
              onClick={handleSubmit}
            >
              Sign Up
            </button>
          </div>
          <p className="forgot-password text-right">
            Already registered <Link to="/user/login">Log In</Link>
          </p>
        </Form>
      </div>
    </div>
  );
};
