import React from "react";
import { Button } from "react-bootstrap";
import { MDBCol, MDBContainer, MDBRow, MDBTypography } from "mdb-react-ui-kit";
import { ORDERS } from "../constants";
import { HomeHeader } from "../header";
import { getUserDataFromLocalStorage } from "../../../token";
import "../home.css";

export const SendPackageCard = ({ history, user = {} }) => {
  const userData = getUserDataFromLocalStorage();
  return (
    <div>
      <div
        style={{ borderBottom: "1px solid lightgray", padding: 10, margin: 5 }}
      >
        <div>
          {user || userData  ? (
            <>
              <p style={{ margin: 0, fontWeight: "bold", fontSize: "x-large" }}>
                Hi {user?.firstName || userData?.firstName}
              </p>
              <p style={{ fontSize: "x-large" }}>
                What would you like to do today ?
              </p>
            </>
          ) : (
            <div> No user found</div>
          )}
        </div>
        <Button
          size="lg"
          style={{
            paddingLeft: 22,
            paddingRight: 22,
            paddingBottom: 10,
            paddingTop: 10,
            margin: 2,
            fontSize: "larger",
          }}
          onClick={() => {
            history.push("/user/send-package");
          }}
        >
          Send package
        </Button>
      </div>
      <div
        style={{ borderBottom: "1px solid lightgray", padding: 20, margin: 10 }}
      >
        {/* Create a orders component for this */}
        <div>
          {ORDERS.map((inst, index) => {
            return (
              <div key={index}>
                <div style={{ fontSize: "small" }}>{inst.description}</div>
                <div style={{ fontSize: "small" }}>
                  {inst.distance} | {inst.weight}
                </div>
                <div>
                  <MDBContainer fluid className="py-5">
                    <MDBRow>
                      <MDBCol lg="12">
                        <div className="horizontal-timeline">
                          <MDBTypography listInLine className="items">
                            {inst.status.map((status, index) => {
                              return (
                                <li className="items-list" key={index}>
                                  <div className="px-4">
                                    <div className="event-date badge bg-success">
                                      {status}
                                    </div>
                                  </div>
                                </li>
                              );
                            })}
                          </MDBTypography>
                        </div>
                      </MDBCol>
                    </MDBRow>
                  </MDBContainer>
                </div>
              </div>
            );
          })}
        </div>
        <Button
          variant="info"
          size="lg"
          style={{
            padding: 10,
            margin: 2,
            fontSize: "larger",
          }}
          onClick={() => {
            history.push("/user/here");
          }}
        >
          View more orders
        </Button>
      </div>
    </div>
  );
};

export const userHome = (props) => {
  return (
    <div>
      <HomeHeader />
      <SendPackageCard
        history={props.history}
        user={props?.location?.state?.userData?.user || {}}
      />
    </div>
  );
};
