import axios from "axios";
// Set the user token in localStorage after a successful login
export const storeUserToken = (token, userData) => {
  localStorage.setItem("userToken", token);
  localStorage.setItem("userData", JSON.stringify(userData));
  axios.defaults.headers.post["Authorization"] = `Bearer ${token}`;
  axios.defaults.headers.get["Authorization"] = `Bearer ${token}`;
  axios.defaults.headers.common["Authorization"] = `Bearer ${token}`;
};

// Retrieve the user token from localStorage
export const getUserToken = () => {
  return localStorage.getItem("userToken");
};

// Retrieve the user token from localStorage
export const getUserDataFromLocalStorage = () => {
  return JSON.parse(localStorage.getItem("userData"));
};

// Remove the user token from localStorage on logout
export const removeUserToken = () => {
  localStorage.removeItem("userToken");
  localStorage.removeItem("userData");
  delete axios.defaults.headers.common["Authorization"];
  delete axios.defaults.headers.post["Authorization"];
  delete axios.defaults.headers.get["Authorization"];
};

// Setting commong header in axios request
export const setAuthTokenInCommonHeader = (token) => {
  if (token) {
    axios.defaults.headers.common["Authorization"] = `Bearer ${token}`;
  } else delete axios.defaults.headers.common["Authorization"];
};

