import React from "react";
import Select from "react-select";


export const CountrySelect = ({
    countries = [],
    selectedCountry = {
      value: "IN",
      label: "🇮🇳 India",
    },
    setSelectedCountry = () => {},
  }) => {
    return (
      <Select
        options={countries}
        value={selectedCountry}
        onChange={(selectedOption) => setSelectedCountry(selectedOption)}
      />
    );
  };