import React, { useState, useCallback } from "react";
import { ReceiverDetails } from "./receiverDetails";
import { PackageDetails } from "./packageDetails";
import "../../login/signup.css";
import { DISTANCE_API_URL, HOST, MAPS_API_URL } from "../../../constants";
import axios from "axios";
import { HomeHeader } from "../../home/header";
import { getUserDataFromLocalStorage, getUserToken } from "../../../token";
import { Booking } from "./bookdelivery";

export const SendPackage = () => {
  // Package details
  const [packageData, setPackageData] = useState({
    packageName: "",
    packageWeight: "",
    packageHeight: "",
    packageLength: "",
    packageBreadth: "",
  });

  // Receiver details object
  const [receiverData, setReceiverData] = useState({
    receiverName: "",
    receiverContact: "",
    receiverPincode: "",
    receiverBuilding: "",
  });

  const [userData, setUserData] = useState(getUserDataFromLocalStorage());

  const [getReceiverDetails, setReceiverDetails] = useState(false);
  const [showBooking, setShowBooking] = useState(false);
  const [errors, setErrors] = useState({});
  const [receiverErrors, setReceiverErrors] = useState({});

  // Search address Component props  - Requires special handling
  const [address, setAddress] = useState("");
  const [selectedAddress, setSelectedAddress] = useState("");
  const [addressList, setAddressList] = useState([]);

  const [selectedVehicle, setVehicle] = useState("");
  const [distanceObj, setDistanceObj] = useState({});
  const handleReceiverDataChange = (e) => {
    const { name, value } = e.target;
    setReceiverData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
    // Resetting errors
    if (receiverData[name]) {
      errors[name] = "";
      setReceiverErrors(errors);
    }
  };

  const handlePackageDataChange = (e) => {
    const { name, value } = e.target;
    setPackageData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
    // Resetting errors
    if (packageData[name]) {
      errors[name] = "";
      setErrors(errors);
    }
  };

  const validatePackageForm = () => {
    let isValid = true;
    const newErrors = {};

    // Validate name
    if (!packageData.packageName.trim()) {
      newErrors.packageName = "Package name is required";
      isValid = false;
    }

    if (!packageData.packageWeight || packageData.packageWeight === 0) {
      newErrors.packageWeight = "Invalid package";
      isValid = false;
    }

    if (!packageData.packageHeight || packageData.packageHeight === 0) {
      newErrors.packageHeight = "Invalid height";
      isValid = false;
    }
    if (!packageData.packageBreadth || packageData.packageBreadth === 0) {
      newErrors.packageBreadth = "Invalid breadth";
      isValid = false;
    }
    if (!packageData.packageLength || packageData.packageLength === 0) {
      newErrors.packageLength = "Invalid length";
      isValid = false;
    }
    setErrors(newErrors);
    return isValid;
  };

  const validateReceiverForm = () => {
    let isValid = true;
    const newErrors = {};

    // Validate name
    if (!receiverData.receiverName.trim()) {
      newErrors.receiverName = "Receiver name is required";
      isValid = false;
    }

    if (!receiverData.receiverBuilding.trim()) {
      newErrors.receiverBuilding = "Receiver building is required";
      isValid = false;
    }
    // Validate Address
    if (!address.trim()) {
      newErrors.receiverAddress = "Receiver address is required";
      isValid = false;
    }
    const phoneRegex = /^\d{10}$/;
    if (
      !receiverData?.receiverContact.trim() ||
      !phoneRegex.test(receiverData.receiverContact)
    ) {
      newErrors.receiverContact = "Enter a valid 10-digit phone number";
      isValid = false;
    }

    if (!receiverData.receiverPincode) {
      newErrors.receiverPincode = "Invalid pincode";
      isValid = false;
    }

    setReceiverErrors(newErrors);
    return isValid;
  };

  const submitBooking = async () => {
    // Check validity of user data
    //  Submit booking functionality
    return;
  };

  // Vehicle type changes
  const maxWalkingDistance = 2000;
  const maxDimensions = {
    walking: [13, 10, 15], // Smaller dimensions for walking
    bicycle: [24, 15, 10],
    electric_moped: [30, 20, 20],
    car: [60, 45, 25],
    pickup_truck: [65, 50, 30],
  };
  const fitsDimensions = (mode) => {
    const dimensions = [
      packageData.packageLength,
      packageData.packageBreadth,
      packageData.packageHeight,
    ];
    return dimensions.every((dim, i) => dim <= maxDimensions[mode][i]);
  };

  const getDeliveryType = async(currentUser) => {
    // // Define a maximum distance for walking, for example, 2 kilometers (2000 meters)
    // GET distance here
    let response = {};
    try {
      response = await axios
          .get(DISTANCE_API_URL, {
            params: {
              transportMode : "car",
              origin : `${currentUser.address.position.lat},${currentUser.address.position.lng}`,
              destination : `${selectedAddress.position.lat},${selectedAddress.position.lng}`
            },
          })
          .then((res) => res.data);
      console.log("response distance api", response);
    } catch (error) {
      console.log("error", error);
    }
    setDistanceObj(response);
    let distance = response?.routes[0]?.sections[0]?.summary?.length || 10;

    // Getting the vehicle type logic
    if (
      packageData.packageWeight <= 5 &&
      fitsDimensions("walking") &&
      distance <= maxWalkingDistance
    ) {
      return "walking";
    } else if (packageData.packageWeight <= 20 && fitsDimensions("bicycle")) {
      return "bicycle";
    } else if (
      packageData.packageWeight <= 50 &&
      fitsDimensions("bicycle")
    ) {
      return "bicycle";
    } else if (packageData.packageWeight <= 150 && fitsDimensions("car")) {
      return "delivery_car";
    } else {
      return "pickup_truck";
    }
    // generate payload and do the transactions
  };

  const handleAddressChange = useCallback(
    async (value) => {
      let response = [];

      setAddress(value);
      if (value) {
        receiverErrors[address] = "";
        setReceiverErrors(errors);
        // GEO code api
        response = await axios
          .get(MAPS_API_URL, {
            params: {
              q: value,
            },
          })
          .then((res) => res.data);
        console.log("response", response);
        setAddressList(response?.items || []);
      } else {
        setAddressList([]);
      }
    },
    [address, selectedAddress, addressList]
  );

  const handleAddressSelect = (e, item) => {
    setSelectedAddress(item);
    setAddressList([]);
  };

  console.log("selected address", selectedAddress);
  const handleBookingView = async (showBooking) => {
    if (!validateReceiverForm()) {
      console.log("Invalid receiver details");
      return;
    }
    // API call
    const currentUser = getUserDataFromLocalStorage();
    // if (!validateReceiverForm()) {
    //   console.log("Invalid receiver details");
    //   return;
    // }
    console.log("submit user functionality");
    // Follow the order of submission in controller / API
    // Submit package details
    // submit receiver details
    // Submit bookings data
    try {
      let response = await axios.post(
        `${HOST}/api/receiver/`,
        {
          name: receiverData.receiverName,
          contact: receiverData.receiverContact,
          pincode: receiverData.receiverPincode,
          userId: currentUser._id, // Fetch this from localstorage
          building: receiverData.receiverBuilding,
          address: selectedAddress,
        },
        {
          headers: {
            Authorization: `Basic ${getUserToken()}`,
          },
        }
      );
      console.log("response signup", response);
      if (response.status === 201) {
        console.log("signup receiver response", response);
        // Call to maps distance api
        let deliveryType = await getDeliveryType(currentUser)
        setVehicle(deliveryType);
        setReceiverDetails(!getReceiverDetails);
        setShowBooking(!showBooking);
      }
    } catch (error) {
      console.log("error", error);
    }
    // Show bookings
  };

  const handlePackageSubmit = () => {
    if (!validatePackageForm()) {
      console.log("Invalid input data");
      return;
    }
    setReceiverDetails(true);
  };

  return (
    <div>
      <HomeHeader />
      <div
        style={{
          backgroundColor: "rgba(0, 0, 0, 0.3)",
          height: "100vh",
        }}
      >
        {getReceiverDetails && (
          <ReceiverDetails
            receiverErrors={receiverErrors}
            handleAddressSelect={handleAddressSelect}
            address={address}
            setAddress={setAddress}
            selectedAddress={selectedAddress}
            setSelectedAddress={setSelectedAddress}
            addressList={addressList}
            setAddressList={setAddressList}
            handleAddressChange={handleAddressChange}
            setReceiverData={setReceiverData}
            handleReceiverDataChange={handleReceiverDataChange}
            showBooking={showBooking}
            handleBookingView={handleBookingView}
          />
        )}
        {showBooking && (
          <Booking
            userData={userData}
            address={address}
            receiverData={receiverData}
            submitBooking={submitBooking}
            selectedVehicle = {selectedVehicle}
            distanceObj = {distanceObj}

          />
        )}
        {!showBooking && !getReceiverDetails && (
          <PackageDetails
            errors={errors}
            handlePackageSubmit={handlePackageSubmit}
            handlePackageDataChange={handlePackageDataChange}
          />
        )}
      </div>
    </div>
  );
};
