import React, { useState } from "react";
import axios from "axios";
import { Form, Button, Alert } from "react-bootstrap";
import "../login.css";
import { HOST } from "../../../constants";
import { storeUserToken } from "../../../token";
import { Link } from "react-router-dom";
import { HomeHeader } from "../../home/header";

export const Login = ({ history }) => {
  const [inputUsername, setInputUsername] = useState("");
  const [inputPassword, setInputPassword] = useState("");
  const [show, setShow] = useState(false);
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (event) => {
    event.preventDefault();
    setLoading(true);
    try {
      let response = await axios.post(`${HOST}/api/user/login/`, {
        email: inputUsername,
        password: inputPassword,
      });
      if (response.status === 200) {
        // Set user token
        console.log("response.data", response.data);
        storeUserToken(response.data.token, response.data.user);
        history.push("/user/home", { userData: response?.data || {} });
      }
    } catch (error) {
      console.log("Error", error);
      setShow(true);
    }
    setLoading(false);
  };

  return (
    <div>
      <HomeHeader />
      <div className="sign-in__wrapper">
        {/* Overlay */}
        <div className="sign-in__backdrop"></div>
        {/* Form */}
        <Form className="shadow p-4 bg-white rounded" onSubmit={handleSubmit}>
          {/* Header */}
          <div className="h4 mb-2 text-center">User login</div>

          {show ? (
            <Alert
              className="mb-2"
              variant="danger"
              onClose={() => setShow(false)}
              dismissible
            >
              Incorrect username or password.
            </Alert>
          ) : (
            <div />
          )}
          <Form.Group className="mb-2" controlId="username">
            <Form.Label>Username</Form.Label>
            <Form.Control
              type="text"
              value={inputUsername}
              placeholder="Username"
              onChange={(e) => setInputUsername(e.target.value)}
              required
            />
          </Form.Group>
          <Form.Group className="mb-2" controlId="password">
            <Form.Label>Password</Form.Label>
            <Form.Control
              type="password"
              value={inputPassword}
              placeholder="Password"
              onChange={(e) => setInputPassword(e.target.value)}
              required
            />
          </Form.Group>
          {!loading ? (
            <Button className="w-100" variant="primary" type="submit">
              Log In
            </Button>
          ) : (
            <Button className="w-100" variant="primary" type="submit" disabled>
              Logging In...
            </Button>
          )}
          <p className="forgot-password text-right">
            New user <Link to="/user/signup">Sign up</Link>
          </p>
        </Form>
      </div>
    </div>
  );
};
